import { isTagEmbed, isTagShop, isSocialWall } from "../../../../actions/api";
import { CloudUrl, WebWidgetUrl } from "../../../../constants";

const ErrorBranding = ({ isLink, wall }) => {
  const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed" || window.location.href.includes('tagembed')
  const logoName = isTagEmbed || isTagEmbedStatus ? 'tagembed.svg' : isTagShop ? 'tagshop.svg' : isSocialWall ? 'socialwalls.svg' : 'tagbox-widget.svg'
  return <div className="tb_error_page_logo">
    <a href={isLink ? isLink : `${isTagEmbedStatus ? `https://tagembed.com/` : WebWidgetUrl}`} target={'_blank'} className="tb_error_page_logo_ico" style={{
      backgroundImage: `url(${CloudUrl}/media/logo/${logoName})`
    }}> </a>
  </div>
}
export default ErrorBranding
