import ErrorBranding from "../Elements/ErrorBranding";
import ReactHtmlParser from 'react-html-parser';
import { isSocialWall } from "../../../../actions/api";
const ErrorWithBranding = ({ title, description, button, isLink, subTitle, error_code }) => {
  const isSocialStatus = isSocialWall && error_code == 401
  return <div className="tb_error_page_wrapper">
    <div className="tb_error_content">
      <div className="tb_error_title">{isSocialStatus ? subTitle : title}</div>
      <div className="tb_error_des">{isSocialStatus ? title : ReactHtmlParser(description)}</div>
      {!isSocialWall ? <div className="tb_error_page_actions">
        <div className="tb_error_action_list">
          {ReactHtmlParser(button)}
        </div>
      </div> : null}
    </div>
    <ErrorBranding isLink={isLink} />
  </div>
}
export default ErrorWithBranding
